@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");
:root {
  --primary-light-cyan: hsl(193, 38%, 86%);
  --primary-neon-green: hsl(150, 100%, 66%);
  --neutral-grayish-blue: hsl(217, 19%, 38%);
  --neutral-dark-grayish-blue: hsl(217, 19%, 24%);
  --neutral-dark-blue: hsl(218, 23%, 16%);
}

p {
  margin: 0.5rem;
}

.App {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  border-radius: 0.5rem;
  max-width: 500px;
  text-align: center;
  background-color: var(--neutral-dark-grayish-blue);
  position: relative;
  padding-bottom: 4rem;
}

.card-header {
  color: var(--primary-neon-green);
  text-transform: uppercase;
  letter-spacing: 2.5px;
  padding: 0 1rem;
  padding-top: 2rem;
}

.card-body {
  color: var(--primary-light-cyan);
  line-height: 1.5;
  font-size: 28px;
  position: relative;
  text-align: center;
  padding: 1.5rem;
}

.divider-container {
  width: 100%;
}

.card-footer {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  bottom: -1.5rem;
}

.card button {
  background: var(--primary-neon-green);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: ease-in-out 0.3s;
}

.card button:hover,
.card button:focus {
  box-shadow: 0 0 1rem var(--primary-neon-green);
  cursor: pointer;
}

.attribution {
  color: var(--primary-light-cyan);
  font-size: 11px;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0.5rem;
}
.attribution a {
  color: var(--primary-neon-green);
}

@media screen and (max-width: 500px) {
  .card {
    max-width: 100%;
    text-align: center;
    background-color: var(--neutral-dark-grayish-blue);
    position: relative;
    padding-bottom: 2rem;
    margin: 1.5rem;
  }

  .card button {
    background: var(--primary-neon-green);
    width: 60px;
    height: 60px;
  }

  .card-header {
    padding: 0 0.5rem;
    padding-top: 1rem;
  }

  .card-body {
    color: var(--primary-light-cyan);
    line-height: 1.5;
    font-size: 28px;
    position: relative;
    text-align: center;
    padding: 1.5 0rem;
  }
}
